/**
 * Date Date String
 * (M)(d)(h)(m)(s)(q)  1-2 
 * (y) 1-4 (S) 1 ( 1-3 )
 *
 * 
 * dateFormat(new Date(),"yyyy-MM-dd hh:mm:ss.S") ==> 2006-07-02 08:09:04.423
 * dateFormat(new Date(),"yyyy-M-d h:m:s.S")      ==> 2006-7-2 8:9:4.18
 * **/

export function dateFormat(date, fmt) {
  var o = {
    'M+': date.getMonth() + 1, //
    'd+': date.getDate(), //
    'h+': date.getHours(), //
    'm+': date.getMinutes(), //
    's+': date.getSeconds(), //
    'q+': Math.floor((date.getMonth() + 3) / 3), //
    'S': date.getMilliseconds() //
  };

  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
  }
  if (/(E+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, ((RegExp.$1.length > 1) ? (RegExp.$1.length > 2 ? "" : "") : "") + week[date.getDay() + ""]);
  }
  for (var k in o) {
    if (new RegExp("(" + k + ")").test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    }
  }
  return fmt;
};



/** * Date Date String * (M)(d)12(h)24(H)(m)(s)(E)(q)
 * 1-2  * (y) 1-4 (S) 1 ( 1-3 ) * eg: * (new
 * pattern("yyyy-MM-dd hh:mm:ss.S")==> 2006-07-02 08:09:04.423
 * pattern(new Date(),"yyyy-MM-dd E HH:mm:ss") ==> 2009-03-10  20:09:04
 * pattern(new Date(),"yyyy-MM-dd EE hh:mm:ss") ==> 2009-03-10  08:09:04
 * pattern(new Date(),"yyyy-MM-dd EEE hh:mm:ss") ==> 2009-03-10  08:09:04
 * pattern(new Date(),"yyyy-M-d h:m:s.S") ==> 2006-7-2 8:9:4.18
 */

export function patternFormat(date, fmt) {
  var o = {
    "M+": date.getMonth() + 1, //
    "d+": date.getDate(), //
    "h+": date.getHours() % 24 == 0 ? 24 : date.getHours() % 24, //
    "H+": date.getHours(), //
    "m+": date.getMinutes(), //
    "s+": date.getSeconds(), //
    "q+": Math.floor((date.getMonth() + 3) / 3), //
    "S": date.getMilliseconds() //
  };
  var week = {
    "0": "",
    "1": "",
    "2": "",
    "3": "",
    "4": "",
    "5": "",
    "6": ""
  };
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
  }
  if (/(E+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, ((RegExp.$1.length > 1) ? (RegExp.$1.length > 2 ? "" : "") : "") + week[date.getDay() + ""]);
  }
  for (var k in o) {
    if (new RegExp("(" + k + ")").test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    }
  }
  return fmt;
};

//
export function getCurrentMonthFirst() {
  var date = new Date();
  date.setDate(1);
  return date;
}
// 
export function getCurrentMonthLast() {
  var date = new Date();
  var currentMonth = date.getMonth();
  var nextMonth = ++currentMonth;
  var nextMonthFirstDay = new Date(date.getFullYear(), nextMonth, 1);
  var oneDay = 1000 * 60 * 60 * 24;
  return new Date(nextMonthFirstDay - oneDay);
}


/**
 * 
 */
export function getweek(n) {
  var now = new Date(); //
  var nowDayOfWeek = now.getDay(); //
  var nowDay = now.getDate(); //
  var nowMonth = now.getMonth(); //
  var nowYear = now.getYear(); //
  nowYear += (nowYear < 2000) ? 1900 : 0; //n=0,6
  var weekStartDate = new Date(nowYear, nowMonth, nowDay + (n - nowDayOfWeek));
  return weekStartDate;
}


//
export function setDays(n) {
  var date = new Date();
  var oneDay = 1000 * 60 * 60 * 24;
  return new Date(date - oneDay * n);
}

//
export function formHoursMin(n) {
  var str = '';
  if (n / 60 >> 0 > 0) {
    str += (n / 60 >> 0) + "";
  }
  if(n%60>0){
    str += (n % 60).toFixed(2) + ""
  }

  return str;
}
/*
 * 2
 * yyyy-mm-dd(2015-01-31)
 * dateUtil.dayMinus(startDate,endDate);
 * @startDate {Date}
 * @endDate {Date}
 * @return endDate - startDate
 */
export function dayMinus(startDate, endDate) {
  if (startDate instanceof Date && endDate instanceof Date) {
    var days = Math.floor((endDate - startDate) / (1000 * 60 * 60 * 24));
    return days;
  } else {
    return "Param error,date type!";
  }
}

//
export  function formHoursMinSecond(n) {
  var str = '';
  if (n /24/ 60/60 >> 0 > 0) {
    str += (n /24/ 60/60 >> 0) + "";
  }
  if (n / 60/60%24 >> 0 > 0) {
    str += (n / 60/60%24 >> 0) + "";
  }
  if(n/60%60>> 0>0){
    str += (n/60%60>> 0) + ""
  }
  if(n%60>0){
    str += (n % 60) + ""
  }
  return str;
}



