import router from "./router";
import store from "./store";
import { asyncRouter } from "@/assets/js/commonManage";

const whiteList = ["/Login"]; //

/**
 * 
 * @returns {Boolea}
 */
const getIsLogin = () => {
  const strCookie = document.cookie;
  //cookie/
  const arrCookie = strCookie.split("; ");

  let islogin;
  //cookiecookie
  for (var i = 0; i < arrCookie.length; i++) {
    var arr = arrCookie[i].split("=");
    //userIdcookie
    if ("islogin" == arr[0]) {
      islogin = arr[1];
      break;
    }
  }
  let type = islogin ? true : false;
  return type;
};

const setRouter = function(callback) {
  let menuList = JSON.parse(localStorage.getItem("gMenuList"));
  // console.log("asyncrouter", asyncRouter(menuList));
  router.addRoutes(asyncRouter(menuList));
  // console.log(1111);
  typeof callback === "function" && callback();
};
router.beforeEach((to, from, next) => {
  console.log("", getIsLogin());
  // console.log(to);
  //
  if (getIsLogin()) {
    if (to.path === "/Login") {
      next({ path: "/" }); //
    } else {
      console.log(to);
      let menList = JSON.parse(localStorage.getItem("gMenuList"));
      if (to.meta.data) {
        let listr = menList.filter(item => {
          return item.parent_id == to.meta.data.id && item.is_button == 1;
        });
        // let permission = {};
        let permission = {
          create: true,
          delete: true,
          read: true,
          update: true
        };

        for (let item of listr) {
          permission[item.permission] = true;
        }
        store.dispatch("setBtnPermission", permission); //vuex
      }

      if (store.getters.getvueLogin) {
        //vuex
        //
        console.log("");
        next();
      } else {
        // 
        console.log("");
        let menuList = JSON.parse(localStorage.getItem("gMenuList"));
        router.addRoutes(asyncRouter(menuList)); //
        store.dispatch("setVueLogin", true); //vuex
        next({ ...to, replace: true }); // hack addRoutes ,replace: true so the navigation will not leave a history record
      }
    }
  } else {
    //
    if (whiteList.indexOf(to.path) !== -1) {
      // 
      next();
    } else {
      next("/Login"); // 
    }
  }
});



