//vuex
export const RESTVUEXSTORE = (state, data) => {
  state.gcount = 0;
  state.gisCollapse = false; //
  state.gTheme = "dark"; //     
  state.gSiderNav = []; //
  state.gNavType = "default"; //
  state.gNavShow = false; //
  state.gVerMenuMap = []; //
  state.gMapLayers = {}; //
  state.gpartsData = []; //
  state.gSocketSendAll = null; //
  state.gSocketMsgByUser = null; ///id
  state.gSocketMsgByModule = null; ///
  state.gVerDefaultIndex = ""; //
  state.gvueLogin = false;
  return (state.gcount = 0);
};

