/**
 * Vue
 * 
 */
import axios from "axios";
import router from "../../router";
import * as validateRule from "./validateRule.js";
import * as commonManage from "./commonManage.js";
import webconfig from "@/webconfig.js";
import { Message } from "element-ui";
export const vueExpand = Vue => {
  Vue.prototype.$http = axios;
  Vue.prototype.$validateRule = validateRule.rules; //
  Vue.prototype.$getList = commonManage.getList;
  Vue.prototype.$submitForm = commonManage.submitForm;
  Vue.prototype.$deleteOne = commonManage.deleteOne;
  Vue.prototype.$deleteBatch = commonManage.deletebatch;
  Vue.prototype.$updateForm = commonManage.updateForm;
  Vue.prototype.$setStore = commonManage.setStore; //localStorage
  Vue.prototype.$getStore = commonManage.getStore; //localStorage
  Vue.prototype.$removeStore = commonManage.removeStore; //localStorage
  Vue.prototype.$getUserData = commonManage.getUserData; //localStorage
  Vue.prototype.$webconfig = webconfig;

  /**
   * axios
   * axios
   */

  axios.interceptors.request.use(
    function(config) {
      // 
      if (!config.isMock) {
        const oldUrl = config.url;
        if (!webconfig.isProduct) {
          config.url = "api" + config.url;
        }
        // config.url = 'api' + config.url //
        // let dict = config.url.includes('initdictionary');
        let login = config.url.includes("isuserlogin");
        // config.headers.access_token = window.localStorage.getItem('TOKEN');
        if (login) {
          delete config.headers.access_token;
        }
        if (config.isInternet) {
          config.url = oldUrl;
          config.headers.post["Content-Type"] =
            "application/x-www-form-urlencoded";

          delete config.isInternet;
          // delete config.headers.access_token
        }
      }
      // console.log("config:", config);
      return config;
    },
    function(error) {
      // 
      return Promise.reject(error);
    }
  );

  // 
  axios.interceptors.response.use(
    function(response) {
      return response;
    },
    function(error) {
      // console.log("error:", error.response);
      let response = error.response;
      switch (response && response.status) {
        case 401:
          TimeOut(``);
          break;
        case 403:
          TimeOut(``);
          break;
        case 404:
          Message({
            showClose: true,
            message: "",
            type: "error"
          });
          break;
        case 500:
          Message({
            showClose: true,
            message: "",
            type: "error"
          });
          break;
        case 501:
          Message({
            showClose: true,
            message: "",
            type: "error"
          });
          break;
        case 502:
          Message({
            showClose: true,
            message: "",
            type: "error"
          });
          break;
        case 503:
          Message({
            showClose: true,
            message: "",
            type: "error"
          });
          break;
        case 504:
          Message({
            showClose: true,
            message: "",
            type: "error"
          });
          break;
        default:
          Message({
            showClose: true,
            message: "",
            type: "error"
          });
          break;
      }
      // 
      return Promise.reject(error);
    }
  );
  const TimeOut = text => {
    var date = new Date();
    date.setTime(date.getTime() - 10000);
    document.cookie = "islogin=true; expires=" + date.toGMTString();
    //                    localStorage.removeItem('ms_username')
    window.localStorage.clear();
    router.push("Login");
    Message({
      showClose: true,
      message: text,
      type: "warning"
    });
  };
};

export const resPonse = obj => {
  return obj;
};



