/**
 * 
 * @param rule
 * @param value
 * @param callback
 * @returns {*}
 * validateRule.js
 */
export const checkNumber = (rule, value, callback) => {
  var reg = /^[0-9]*$/;//
  if (!value) {
    callback(new Error(''));
  }
  setTimeout(() => {
    if (!reg.test(value)) {
      callback(new Error(''));
    }
    else {
      if (value.length > 13) {
        callback(new Error('13'));
      }
      else {
        callback();
      }
    }
  }, 300);
};
/**
 * 
 * @param rule
 * @param value
 * @param callback
 */
export const checkAge = (rule, value, callback) => {

  var reg = /^[1-9]\d{1,2}$/;//
  if (!value) {
    callback(new Error(''));
  }
  setTimeout(() => {

    if (!reg.test(value)) {
      callback(new Error(''));
    } else if (value < 18) {
      callback(new Error('18'));
    }
    else {
      callback();
    }

  }, 100);
};
