export const isMobilePhone = (rule, value, callback) => {
  var reg = /^1[34578]\d{9}$/;//
  setTimeout(() => {
    if (!reg.test(value)) {
      callback(new Error('18723346175'));
    } else {
      callback();
    }
  }, 300);
};

/**
 * 
 * @param rule
 * @param value
 * @param callback
 * @returns {*}
 */
export const checkFloat = (rule, value, callback) => {
  // var reg = /^(-?\d+)(\.\d{0,5})?$/;//
  var reg = /^(-?[1-9]\d*(\.\d*[1-9])?)$|^(-?0\.\d*[1-9])$|^0$/;

  if (!value && value != 0) {
    callback(new Error(''));
  }
  setTimeout(() => {
    if (!reg.test(value)) {
      callback(new Error('-2.33'));
    } else {
      callback();

    }
  }, 300);
};

